body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.logo-name {
  font-weight: 600;
  margin: 0 15px;
}

.nav-text {
  font-weight: 450;
  font-size: 1.25rem;
}

.content {
  padding-top: 20px;
  flex: 1 1 auto;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 0,5rem;
  background: rgb(34,64,195);
  background: linear-gradient(0deg, rgba(34,64,195,1) 0%, rgb(16, 18, 128) 100%) no-repeat;
  background-size: 100%;
  width: 100%;
  color: white;
  max-width: 100%;
  height: 100%;
  min-height: calc(100vh - 70px - 64px);
}

.footer-info {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  color: white;
  width: 100%;
  height: 70px;
  background-color: #303f9f;
}

.footer-info h4 {
  margin: 20px 25px 20px;
  font-weight: 400;
  font-size: 36px;
}

.footer-info a {
  color: rgba(255, 255, 255, 0.897);
  margin: 20px 40px 25px;
  font-weight: 400;
  font-size: 36px;
}

.card .card-content {
  background-color: inherit;
  height: 90px;
  color: black;
  background-color: rgb(228, 225, 225);
  width: 200px;
  padding: 12px; 
  border-radius: 0 0 5px 5px;
}

.card-content p {
  width: 100%;
}

.card {
  -webkit-box-shadow: 23px 25px 17px -3px rgba(0, 0, 0, 0.22);
  -moz-box-shadow: 23px 25px 17px -3px rgba(0, 0, 0, 0.22);
  box-shadow: 23px 25px 17px -3px rgba(0, 0, 0, 0.22);
  cursor: pointer;
}

.card:hover {
  opacity: 0.8;
  transition: all 0.3s ease-in-out;
}

.desc-content {
  display: flex;
  max-width: 50%;
  flex-direction: column;
  align-content: center;
  flex-wrap: wrap;
  align-items: flex-start;
  align-content: center;
}

.description-content {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 20px;
  flex: 1 1 auto;
  background: rgb(34,64,195);
  background: linear-gradient(0deg, rgba(34,64,195,1) 0%, rgb(16, 18, 128) 100%) no-repeat;
  background-size: 100%;
  width: 100%;
  color: white;
  max-width: 100%;
  height: 100%;
  min-height: calc(100vh - 70px - 64px);
}

.name-table {
  font-weight: 400;
  font-size: 22px;
}

.desc-img {
  height: 450px;
  width: 450px;
}

.desc-name {
  font-size: 40px;
  font-weight: 600;
}

.description {
  font-size: 24px;
  font-weight: 500;
}

.preloader-wrapper {
  justify-self: center;
}